.detailedJob{
    display: flex;
    flex-direction: column;
    width: 100vw;
    padding-top: 9vh;
    padding-bottom: 9vh;
    p{
        line-height: 1.2;
        padding: 0 !important;
        max-width: none !important;
    }
    h2{
        margin-bottom: 2rem;
        text-align: start;
    }
    .container-detailedJob{
        display: flex;
        width: 100%;
        height: 100%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0% 15%;
        .info{
            width: 100%;
            text-align: start;
            margin-bottom: 2rem;
        }
        .section-vignette{
            display: flex;
            width: 100%;
            flex-direction: column;
            margin-bottom: 2rem;
            .box-vignette{
                width: 100%;
                display: flex;
                flex-direction: row;
            }
            .vignette{
                width: 0.4rem;
                height: 0.4rem;
                background-color: black;
                border-radius: 50%;
                margin-right: 1rem;
                margin-top: 1.2rem;
            }
            .text-vignette{
                width: 100%;
                text-align: start;
                margin-bottom: 0.5rem;
            }
        }
    }
}
@include breakpoint($desktop){
    .detailedJob{
        .container-detailedJob{
            .btn-apply{
                font-size: 1.45rem;
            }
        }
    }
}
@include breakpoint($tablet){
    .detailedJob{
        .container-detailedJob{
            .btn-apply{
                font-size: 1.2rem;
                height: 30px;
            }
            .section-vignette{
                .vignette{
                    margin-top: 1rem;
                }
            }
        }
    }
}
@include breakpoint($sm-tablet){
    .detailedJob{
        .container-detailedJob{
            padding: 0% 10%;
            .section-vignette{
                .vignette{
                    margin-right: 0.6rem;
                    margin-top: 0.9rem;
                }
            }
        }
    }
}
@include breakpoint($lg-phone){
    .detailedJob{
        .container-detailedJob{
            .section-vignette{
                .vignette{
                    margin-right: 0.4rem;
                    margin-top: 0.75rem;
                }
            }
            h2{
                margin-bottom: 1rem;
            }
            .info{
                margin-bottom: 1rem;
            }
            .section-vignette{
                margin-bottom: 1rem;
            }

        }
    }
}