.ourBenefits {
    display: flex;
    flex-direction: column;
    width: 100vw;
    padding-top: 9vh;
    .container-ourBenefits {
      z-index: 2;
      display: flex;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .box {
        width: 100%;
        height: 80%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding: 0% 20%;
        h1{
          padding: 0;
        }
        .box-benefits{
            width: 100%;
            p{
                width: 100%;
                text-align: left;
                .TextHighlighted{
                    color: $logo;
                    font-weight: bold;
                }
            }
        }
      }
    }
  }
  @include breakpoint($sm-tablet) {
    .ourBenefits{
      height: 100%;
      .container-ourBenefits{
        .box{
          padding: 0 15%;
          h1{
            margin-bottom: 3rem;
          }
          .box-benefits{
            margin-bottom: 2rem;
          }
        }
      }
    }
  }
  @include breakpoint($phone) {
    .ourBenefits{
      .container-ourBenefits{
        .box{
          padding: 0 10%;
        }
      }
    }
  }
  