.image-background-container {
  position: relative;

  img {
    position: absolute;
    z-index: -1;
  }

  #bottom {
    max-width: 864px;
    right: -10vw;
    bottom: -100vh;

    @include breakpoint($phone) {
      height: 120vh;
    }
  }
}
.d-none {
  display: none;
}
section {
  height: 100vh;
  width: 100vw;
}
.App {
  overflow: hidden;
}
h1 {
  padding: 4% 0 0% 0%;
  font-family: "League Spartan", sans-serif;
  font-size: 7rem;
  @include breakpoint($desktop) {
    font-size: 5.5rem;
  }
  @include breakpoint($tablet) {
    font-size: 4rem;
    padding: 6% 5% 5% 5%;
  }
  @media screen and (max-height: $sm-tablet) and (min-width: $desktop) {
    padding: 2% 0 1% 0%;
  }
  @include breakpoint($sm-tablet) {
    font-size: 3rem;
  }
  @include breakpoint($phone) {
    font-size: 2.4rem;
  }
  @media screen and (min-width: $desktop) and (min-height: $phone) and (max-height: $sm-tablet) and (orientation: landscape) {
    font-size: 4.5rem;
  }
}

h2{
  padding: 0;
  font-size: 3rem;
  font-weight: bold;
  color: $logo;
  @include breakpoint($tablet) {
    font-size: 2.6rem;
  }
  @include breakpoint($sm-tablet) {
    font-size: 2.2rem;
  }
  @include breakpoint($lg-phone) {
    font-size: 2rem;
  }
}

p {
  font-family: "Glacial Indifference Regular";
  font-size: 2.8rem;
  width: 550px;
  align-self: center;
  @include breakpoint($desktop) {
    font-size: 2.4rem;
  }
  @include breakpoint($sm-desktop) {
    width: 500px;
    font-size: 2.3rem;
  }
  @include breakpoint($tablet) {
    font-size: 2rem;
  }
  @include breakpoint($sm-tablet) {
    font-size: 1.8rem;
  }
  @include breakpoint($lg-phone) {
    font-size: 1.5rem;
  }
  @media screen and (min-width: $desktop) and (min-height: $phone) and (max-height: $sm-tablet) and (orientation: landscape) {
    font-size: 2.5rem;
  }
}
.flex {
  display: flex;
}
.center-h {
  justify-content: center;
}
.space {
  justify-content: space-around;
}
