.nav {
  height: 80px;
  width: 100%;
  display: flex;
  flex-direction: row;
  z-index: 10;
  position: fixed;
  .none {
    display: none;
  }
  .img-logo1 {
    display: none;
  }
  .container {
    display: flex;
    flex-direction: row;
    margin-left: 6vw;
    margin-top: 6vh;
    position: absolute;
    .img-n_blue {
      height: 10vh;
      align-self: center;
    }
    .title-nav {
      font-size: 4rem;
      font-weight: bolder;
      color: $primary;
      align-self: flex-end;
      line-height: 1.3;
      margin-left: 3px;
      text-align: left;
    }
    .img-logo {
      height: 60px;
      width: 60px;
    }
  }
  .container-scroll {
    margin-top: 10px;
  }
  .menu-icon {
    display: none;
  }
  ul {
    position: absolute;
    display: flex;
    right: 0;
    align-items: center;
    height: 80px;
    margin-top: 0;
    min-width: 45%;
    margin-right: 3vw;
    justify-content: space-between;
    .contact {
      margin-top: 0;
      margin-left: 0;
      padding-left: 20px;
      padding-right: 20px;
      font-size: 2rem;

      @include breakpoint($desktop) {
        font-size: 1.45rem;
      }
    }
    li {
      list-style: none;
      margin-right: 5px;

      font-weight: 900;
      color: $logo;
      cursor: pointer;

      a {
        color: $primary-light;
        text-decoration: none;
        font-size: 2rem;

        @include breakpoint($desktop) {
          font-size: 1.45rem;
        }

        &:active {
          color: $secondary;
        }
        &:hover {
          color: $secondary;
        }
      }
    }
    .is-current {
      a {
        color: $secondary;
      }
      color: $secondary;
    }
  }
  .ul-details{
    min-width: 20%;
  }
}
.select-language {
  width: 50px;
  .selected-language {
    align-items: center;
    justify-content: center;
    display: flex;
    font-size: 2.4rem;
    border-radius: 5px;
    cursor: pointer;
  }
  .language-items-container {
    position: absolute;
    border-radius: 5px;
  }
  .language-item {
    align-items: center;
    justify-content: center;
    display: flex;
    font-size: 2.2rem;
    cursor: pointer;
    margin-top: 0.6rem;
  }
  .flag-us {
    margin-right: 7px;
  }
}
.nav-scroll {
  background-color: $white;
  box-shadow: 0px 2px $shadow;
}

@include breakpoint(1300px) {
  .nav {
    ul {
      width: 57vw;
    }
    .ul-details{
      min-width: 10vw !important;
      width: 250px !important;
    }
  }
}
@include breakpoint($tablet) {
  .nav-scroll {
    box-shadow: none;
  }
  .nav {
    height: 9vh;
    min-height: 50px;
    max-height: 100px;
    ul {
      display: none;
    }
    .ul-details{
      display: flex;
      height: 9vh;
      min-height: 50px;
      max-height: 100px;
      .contact{
        font-size: 1.2rem;
        height: 30px;
      }
    }
    .container {
      .img-n_blue {
        display: none;
      }
      .title-nav {
        display: none;
      }
      .img-logo {
        height: 7vh;
        min-height: 40px;
        max-height: 80px;
      }
    }
    .menu-icon {
      display: flex;
      width: 4vh;
      height: 4vh;
      align-items: center;
      position: absolute;
      right: 5vw;
      top: 2.5vh;
      justify-content: center;
      .img-menu {
        width: 80%;
        position: absolute;
      }
    }
    .img-logo1 {
      display: flex;
      height: 7vh;
      margin-left: 6vw;
      margin-top: 10px;
    }
  }
  .menu {
    height: 100vh;
    position: fixed;
    right: 0;
    background-color: $white;
    z-index: 3;
    width: 40%;
    .container {
      height: 60vh;
      margin-top: 15vh;
      .text-blue {
        color: $logo;
        font-size: 1.5rem;
        margin-left: 40px;
        font-weight: 400;
        text-align: left;
      }
      .language-items-container {
        background-color: rgba(34, 104, 175, 0.15);

        .language-item {
          display: flex;
          align-items: center;
          height: 60px;
          padding-left: 26px;
          color: $logo;
          font-size: 1.5rem;
          font-weight: 400;
          .text-blue {
            margin-left: -12px;
          }
        }
        .language-item :hover {
          color: #ff8c00;
          font-weight: bold;
        }
      }
      .row-section {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 60px;
        padding-left: 10%;
        text-decoration: none;

        .arrow-blue {
          right: 30px;
          width: 17px;
          transform: rotate(180deg);
          transition: transform 0.5s;

          &.rotate {
            transform: rotate(0deg);
          }
        }
      }
      .row-section:hover {
        background-color: rgba(34, 104, 175, 0.23);
      }
    }
    .icon-blue {
      width: 25px;
      height: auto;
      position: absolute;
      padding-top: 0px;
    }
    .icon-blue2 {
      width: 25px;
      position: absolute;
      bottom: 2vh;
      right: 20px;
    }
    .icon-blue3 {
      width: 25px;
      position: absolute;
      bottom: 2vh;
      right: 60px;
    }
    .container2 {
      height: 25vh;
      align-items: flex-end;
      justify-content: flex-end;
    }
    .contact {
      background-color: #deb887;
    }
  }
}

@include breakpoint($sm-tablet) {
  .menu {
    width: 50%;
  }
}
@include breakpoint($sm-tablet) {
  .menu {
    width: 50%;
  }
}
@include breakpoint($phone) {
  .menu {
    width: 70%;
  }
  .nav{
    .ul-details{
      width: 200px !important;
    }
  }
}
@include breakpoint($sm-phone) {
  .menu {
    width: 80%;
  }
}
