.openRoles {
    display: flex;
    flex-direction: column;
    width: 100vw;
    padding-top: 9vh;
    padding-bottom: 9vh;
    height: 100%;
    position: relative;
    .back-left{
      position: absolute;
      width: 20%;
      left: 0;
      top: -60vh;
    }
    .back-right{
      position: absolute;
      width: 20%;
      right: 0;
      bottom: -5vh;
    }
    .container-openRoles {
      z-index: 2;
      display: flex;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .box {
        width: 100%;
        height: 80%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding: 0% 20%;
        h1{
          padding: 0;
        }
        .info-roles{
            margin-bottom: 1rem;
            max-width: 100%;
        }
        .btn-roles{
          display: flex;
          flex-direction: column;
          height: auto;
          width: 100%;
          box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
          border-radius: 20px;
          padding: 2vw;
          background-color: #FFF8F1;
          margin-bottom: 1rem;
        }
        .btn-title{
            text-align: start;
            width: 100%;
        }
        .btn-modality{
            text-align: start;
            width: 100%;
            font-weight: 100;
            color: #000;
        }

      }
    }
}
  @include breakpoint($tablet) {
    .openRoles{
      .back-left{
        width: 30%;
        top: -30vh;
      }
      .back-right{
        width: 30%;
        bottom: 0;
      }
      .container-openRoles{
        .box{
          .btn-roles{
            padding: 2vw 3vw;
          }
          .btn-modality{
            padding: 0;
            max-width: none;
          }
        }
      }
    }
  }
  @include breakpoint($lg-phone) {
    .openRoles{
      .container-openRoles{
        .box{
          padding: 0 15%;
        }
      }
    }
  }
  @include breakpoint($phone) {
    .openRoles{
      .back-left{
        top: -15vh;
      }
      .back-right{
        width: 35%;
        bottom: 0;
      }
    }
  }
  