.howIsOurTeam {
    display: flex;
    flex-direction: column;
    width: 100vw;
    padding-top: 9vh;
    position: relative;
    .back-left{
      position: absolute;
      width: 20%;
      left: 0;
      top: 50vh;
    }
    .container-howIsOurTeam {
      z-index: 2;
      display: flex;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .box {
        width: 100%;
        height: 80%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        padding: 0% 10%;
        h2{
          padding: 0;
          font-size: 3rem;
          font-weight: bold;
          color: $logo;
        }
        .box-firstText{
            width: 60vw;
            p{
                width: 100%;
                text-align: left;
                .TextHighlighted{
                    color: $logo;
                    font-weight: bold;
                }
            }
        }
        .box-secondText{
            align-self: flex-end;
            width: 50vw;
            p{
                width: 100%;
                text-align: right;
            }
        }
      }
    }
  }
.photoSection{
  display: flex;
  flex-direction: column;
  width: 100vw;
  padding-top: 9vh;
  padding-bottom: 9vh;
  height: 100% !important;
  .container-howIsOurTeam {
    z-index: 2;
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .img-box{
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      padding: 0% 10%;
      .img-spacer{
        height: 4vh;
      }
      .img-row{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .img-team{
          width: 48%;
          max-width: 560px;
        }
      }
    }
  }  
}
  @include breakpoint-min($desktop){
    .photoSection{
      .container-howIsOurTeam{
        .img-box{
          .img-row{
            justify-content: center;
            .img-team{
              margin: 0 2vh;
            }
          }
        }
      }
    }
  }
  @include breakpoint($sm-tablet) {
    .howIsOurTeam {
      height: 100%;
      .back-left{
        width: 30%;
        top: 20vh;
      }
      .box{
        h2{
          margin-bottom: 2rem;
        }
        .box-firstText{
          margin-bottom: 3rem;
        }
      }
    }
    .photoSection{
      padding-bottom: 0;
      .container-howIsOurTeam {
        .img-box{
          .img-row{
            flex-direction: column;
            align-items: center;
            .img-team{
              width: 100%;
              margin-bottom: 4vh;
            }
          }
          .img-spacer{
            height: 0;
          }
        }
      }  
    }
  }
  @include breakpoint($phone) {
    .howIsOurTeam {
      .back-left{
        width: 35%;
        top: 40vh;
      }
    }
  }
  