.masterhead {
  display: flex;
  height: 100vh;
  background-image: url("../../assets/images/1-all-2-top-left.svg"),
    url("../../assets/images/1-left-2.svg");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: 160vh, 55vw;

  @include breakpoint($tablet) {
    background-image: url("../../assets/images/1-left-2.svg");
    background-position-y: 0;

    background-size: 100vw;
  }
  img {
    z-index: 0;
  }
  .btn {
    text-decoration: none;
    padding: 5px 30px;
    margin-top: 20px;
    display: none;
    cursor: pointer;
    @include breakpoint($tablet) {
      display: block;
    }
  }
  .img-background {
    position: absolute;
    width: 65vw;
    height: 105vh;
    top: -2vh;
    left: -5vw;
  }
  .img-back_2 {
    position: absolute;
    z-index: -1;
    height: 60vh;
    right: 0;
    bottom: -40%;
  }
  .container {
    width: 60vw;
    height: 100vh;
  }
  .img-logo {
    position: absolute;
    right: 2vw;
    width: 22vh;
    bottom: 3%;
    z-index: 0;
  }
  .img-group {
    width: 60vh;
    position: absolute;
    left: 8vw;
    top: 45vh;
  }
  .img-back_home1 {
    display: none;
  }
  .img-background2 {
    display: none;
  }
  .img-logo2 {
    display: none;
  }
  .container {
    display: flex;
    justify-content: flex-end;
    width: 100vw;
    .text {
      justify-content: center;
      margin-right: 10vw;
      text-align: right;
      max-width: 500px;
      color: $logo;
      font-size: 4rem;

      @include breakpoint($tablet) {
        display: flex;
        position: relative;
        color: $logo;
        font-size: 2rem;
        max-width: 80vw;
        margin-top: 4vh;
        text-align: center;
        margin-left: 0;
        margin-right: 0;
      }
    }
  }
}
@include breakpoint($tablet) {
  .masterhead {
    .img-background {
      display: none;
    }
    .img-back_2 {
      bottom: -55%;
      height: 78vh;
    }
    .img-logo {
      display: none;
    }
    .container {
      display: flex;
      width: 100vw;
      height: 100vh;
      flex-direction: column;
      align-items: center;
    }
    .img-back_home1 {
      display: flex;
      position: absolute;
      width: 100vw;
      height: 105%;
    }
    .img-background2 {
      display: flex;
      position: absolute;
      height: 70%;
      right: 0;
      bottom: -50%;
    }
    .img-logo2 {
      display: flex;
      position: relative;
      height: 16vh;
      margin-top: 12vh;
    }

    .img-group {
      height: 34vh;
      width: auto;
      position: relative;
      right: 0;
      left: 0;
      top: -6vh;
      margin-top: 11vh;
    }
  }
}
