#ourServices {
  width: 100% !important;
  background-image: url("../../assets/images/3-bottom-4-top-blue-left.svg"),
    url("../../assets/images/bottomRight.svg");
  background-position-y: -58vh, 79.1vh;
  background-repeat: no-repeat;
  background-size: 60vh, 78vh;
  background-position-x: 0, right;
  display: flex;
  flex-direction: column;

  padding-top: 9vh;

  @include breakpoint($tablet) {
    background-image: url("../../assets/images/responsive-clients-right.svg");
    background-size: 50vh;
    background-position: 18vh 81vh;
    background-position-x: right;
    justify-content: space-around;
  }
  .os-background1 {
    position: absolute;
    height: 60vh;
    top: 0;
  }
  .os-background2 {
    position: absolute;
    height: 25vh;
    width: 60vw;
    right: 0px;
    bottom: 0px;
  }

  .sub-title {
    display: flex;
    align-self: center;
  }
  .services {
    width: 100%;
    padding-left: 50px;
    display: flex;
    .service {
      position: relative;
      width: 20vw;
      height: 50vh;
      top: 10vh;
      .service-background {
        position: absolute;
        width: 14vw;
      }
      .service-gif {
        position: absolute;
        width: 11vw;
        margin-top: 30px;
        margin-left: 10px;
      }
      .service-title {
        padding-top: 80%;
        div {
          font-family: "Glacial Indifference Regular";
          font-size: 2.7rem;
          margin-right: 35%;

          @include breakpoint($desktop) {
            font-size: 2.3rem;
          }
        }
      }
    }
  }
  .service-carousel {
    display: flex;
    flex-direction: column;
    align-items: center;

    h3 {
      font-size: 24px;
      text-align: center;
    }
    .service-background {
      position: absolute;
      width: 52%;
    }
    .service-gif {
      position: absolute;
      width: 45%;
      margin-top: 30px;
      margin-left: 10px;
    }
    .service-title {
      margin-top: 60%;
      padding: 0 41%;
      div {
        font-family: "Glacial Indifference Regular";
        @include breakpoint($sm-tablet) {
          font-size: 2rem;
        }
        @include breakpoint($phone) {
          font-size: 1.8rem;
        }
        @include breakpoint($sm-phone) {
          font-size: 1.5rem;
        }
      }
    }
  }
  .react-multi-carousel-list {
    display: none;
    padding-top: 10%;
    align-items: center;
  }
  .react-multiple-carousel__arrow {
    border-radius: 0;
    width: 33px;
    height: 100%;
    min-width: 33px;
    min-height: 33px;
    z-index: 2;
    background: rgba($color: $white, $alpha: 0);
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  }
  .react-multiple-carousel__arrow::before {
    color: $primary-light;
    font-weight: bolder;
    font-size: 40px;
    height: 40%;
  }

  .react-multiple-carousel__arrow:hover {
    background: rgba($color: $white, $alpha: 0);
  }
  .react-multiple-carousel__arrow--right {
    right: 5%;
  }
  .react-multiple-carousel__arrow--left {
    left: 5%;
  }
}

@include breakpoint($tablet) {
  .react-multi-carousel-list {
    display: flex !important;
  }
  .services {
    display: none !important;
  }
}
