.hiringAbout {
    display: flex;
    width: 100vw;
    padding-top: 9vh;
    &#hiringAbout-us-1 {
      @include breakpoint($lg-tablet) {
        h1 {
          padding: 12% 21% 3% 21%;
        }
      }
    }
    .back-right{
      position: absolute;
      width: 20%;
      right: 0;
    }
    .img-programs {
      position: absolute;
      z-index: 1;
      height: 95vh;
    }
    .img-back {
      position: absolute;
      z-index: -1;
      height: 85vh;
      margin-top: 5vh;
    }
    .img-program1 {
      display: none;
    }
    .img-program2 {
      display: none;
    }
    .img-back_program {
      display: none;
    }
    .btn-hiringAbout {
      margin-top: 5%;
      padding: 3rem 5rem;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50px;
      font-size: 2.2rem;
      text-decoration: none;
      margin-bottom: 7%;
      @include breakpoint($tablet) {
        padding: 5px 30px;
        font-size: 1.8rem;
      }
    }
    .container-hiringAbout {
      z-index: 2;
      display: flex;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .title {
        font-size: 4rem;
        font-weight: bold;
        padding-left: 5vw;
        width: 70%;
        color: $primary;
      }
      .title2 {
        padding-left: 0vw;
        width: 100%;
      }
      .text {
        font-size: 1.7rem;
        text-align: center;
        width: 50%;
        margin-top: 20px;
        padding-left: 5vw;
        color: $primary;
      }
      .text2 {
        width: 100%;
        padding-left: 0vw;
        font-size: 2rem;
      }
      .img-team {
        width: 40%;
        margin-top: 1rem;
        align-self: center;
      }
      .box {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        h1{
          padding: 0;
        }
        p{
          width: 80vw;
        }
      }
      .box2 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
      .info {
        display: flex;
        justify-content: flex-end;
        width: 100%;
        overflow: hidden;
        & * {
          margin: 0 3%;
        }
        .item-info {
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
        }
  
        .info-text {
          display: flex;
          font-size: 1.3rem;
          color: $gray;
          font-weight: bold;
          margin-left: -12px;
          width: auto;
          justify-content: center;
          align-items: center;
          text-decoration: none;
          min-width: 200px;
        }
        #linkedin {
          -webkit-mask: url("../../assets/linkedin_gray.svg");
          mask: url("../../assets/linkedin_gray.svg");
        }
        #mail {
          -webkit-mask: url("../../assets/email_gray.svg");
          mask: url("../../assets/email_gray.svg");
        }
        #phone {
          -webkit-mask: url("../../assets/phone_gray.svg");
          mask: url("../../assets/phone_gray.svg");
        }
        #phone,
        #mail,
        #linkedin {
          mask-size: cover;
          background: #7d8b8f;
          width: 70px;
          height: 40px;
          &:hover {
            background: $secondary;
          }
        }
        a:hover {
          color: $secondary;
        }
      }
    }
  }
  @include breakpoint($tablet) {
    .hiringAbout {
      .back-right{
        width: 30%;
      }
      .img-programs {
        display: none;
      }
      .img-back {
        display: none;
      }
      .title {
        width: 80%;
      }
      .text {
        width: 75%;
      }
      .img-program1 {
        display: flex;
        position: absolute;
        height: 43vh;
        right: 2vw;
      }
      .img-back_program {
        display: flex;
        position: absolute;
        height: 100vh;
        left: 0;
        bottom: -140vh;
      }
      .img-program2 {
        display: flex;
        position: absolute;
        height: 40vh;
        left: 0;
        bottom: -100vh;
      }
      .back-down {
        height: 70;
        bottom: -245vh;
        left: -13vw;
      }
      .container-hiringAbout {
        .box2 {
          display: flex;
          height: 100%;
          flex-direction: column;
        }
        .box {
          width: 60%;
          margin-bottom: 5vh;
        }
        .img-team {
          width: 70%;
        }
        .info {
          display: none;
          height: 0%;
        }
      }
    }
  }
  @include breakpoint($sm-tablet) {
    .hiringAbout {
      height: 100%;
      .container-hiringAbout {
        .title {
          font-size: 3rem;
        }
        .text {
          font-size: 1.4rem;
        }
        .info {
          .info-text {
            font-size: 1rem;
          }
        }
      }
    }
  }
  @include breakpoint($lg-phone) {
    .hiringAbout {
      .container-hiringAbout {
        .title {
          font-size: 3rem;
        }
        .text {
          font-size: 1.4rem;
          width: 60%;
        }
        .text2 {
          width: 100%;
        }
      }
    }
  }
  @include breakpoint($phone){
    .hiringAbout{
      .back-right{
        width: 35%;
      }
    }
  }
  