footer {
  height: 9vh;
  width: 100vw;
  background-color: $logo;
  display: flex;
  justify-content: space-between;
  padding: 0 5%;
  position: relative;
  bottom: 0;
  align-items: center;
  .footer-img {
    img {
      width: 5rem;
      @include breakpoint($desktop) {
        width: 3.5rem;
      }
    }
  }
  h3 {
    color: white;
    font-family: "Glacial Indifference Regular";
    font-size: 1.5rem;
    align-self: flex-end;
    padding-bottom: 15px;
  }
  .footer-icons {
    & * {
      margin: 1rem 0;
    }
    img {
      width: 2.3rem;
      @include breakpoint($desktop) {
        width: 1.5rem;
      }
    }
  }
  @include breakpoint($sm-tablet) {
    .footer-img img {
      width: 45px;
    }
    h3 {
      font-size: 1.2rem;
      @include breakpoint($phone) {
        font-size: 1rem;
      }
    }
    .footer-icons {
      & * {
        margin: 8px 0;
      }
      img {
        width: 20px;
      }
    }
  }
}
