#clients {
  height: calc(100vh - 9vh);
  flex-direction: column;
  background-image: url("../../assets/images/bottomRight.svg");
  background-position: bottom right;
  background-repeat: no-repeat;
  background-size: 79vh;
  padding-top: 7vh;
  @include breakpoint($tablet) {
    background-image: url("../../assets/images/responsive-clients-left.svg"),
      url("../../assets/images/responsive-clients-right.svg");
    background-size: auto, 50vh;
    background-position: bottom left, 18vh -19vh;
    background-position-y: bottom, -19vh;
    background-position-x: left, right;
    padding-top: 7vh;
  }

  .gif {
    padding: 2rem 6rem;
    max-width: 45%;
    align-self: center;
    @include breakpoint($desktop) {
      padding: 2rem 7rem;
    }
    @include breakpoint($tablet) {
      max-width: 100%;
      min-height: 180px;
      padding: 2rem 2.5rem;
    }
    @include breakpoint($phone) {
      padding: 1rem 2rem;
    }
    @include breakpoint($sm-phone) {
      max-height: 200px;
    }
    @media screen and (max-height: $phone) and (orientation: landscape) {
      min-height: 162px;
      max-width: 45%;
    }
  }

  .container-padding-bottom,
  .container-with-dots {
    width: 90%;
    align-self: center;
    li.react-multi-carousel-item {
      align-items: center;
      display: flex;
      justify-content: center;
    }
    img {
      max-height: 80px;
      max-width: 200px;
      min-height: auto;
      align-self: center;
    }
    @include breakpoint($tablet) {
      padding-bottom: 4%;
      img {
        max-height: 60px;
        max-width: 100px;
        min-height: auto;
        align-self: center;
      }
    }
  }
}
