.hiringMasterhead {
    display: flex;
    height: 100vh;
    background-image: url("../../assets/images/back-top.svg"),
      url("../../assets/images/back-left.svg");
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 160vh, 160vh;
  
    @include breakpoint($tablet) {
      background-image: url("../../assets/images/1-left-2.svg");
      background-position-y: 0;
  
      background-size: 100vw;
    }
    img {
      z-index: 0;
    }
    .btn {
      text-decoration: none;
      padding: 5px 30px;
      margin-top: 20px;
      margin-right: 5vw;
      cursor: pointer;
      @include breakpoint($tablet) {
        margin-right: 0;
        display: block;
      }
    }
    .img-background {
      position: absolute;
      width: 65vw;
      height: 105vh;
      top: -2vh;
      left: -5vw;
    }
    .img-back_2 {
      position: absolute;
      z-index: -1;
      height: 60vh;
      right: 0;
      bottom: -40%;
    }
    .container {
      width: 60vw;
      height: 100vh;
    }
    .img-logo {
      position: absolute;
      right: 4vw;
      width: 22vh;
      bottom: 3%;
      z-index: 0;
    }
    .img-group {
      display: none;
    }
    .img-back_home1 {
      display: none;
    }
    .img-background2 {
      display: none;
    }
    .img-logo2 {
      display: none;
    }
    .container {
      display: flex;
      justify-content: flex-end;
      width: 100vw;
      .homeInfo{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding-bottom: 5vh;
      }
      .title {
        justify-content: center;
        margin-right: 5vw;
        text-align: center;
        color: $logo;
        font-size: 5rem;
        font-weight: bold;
      }
      .text {
        justify-content: center;
        margin-right: 6vw;
        text-align: center;
        width: 740px;
        font-size: 4rem;
        @include breakpoint($tablet) {
          display: flex;
          position: relative;
          font-size: 2rem;
          max-width: 80vw;
          text-align: center;
          margin-left: 0;
          margin-right: 0;
        }
      }
    }
  }
  .img-group-aux {
    width: 56vh;
    position: absolute;
    left: 10vw;
    top: 40vh;
  }
  @include breakpoint($desktop){
    .hiringMasterhead{
      .container{
        .title{
          font-size: 4rem;
        }
        .text{
          font-size: 3rem;
          width: 500px;
        }
      }
    }
  }
  @include breakpoint($tablet) {
    .hiringMasterhead {
      .img-background {
        display: none;
      }
      .img-back_2 {
        bottom: -55%;
        height: 78vh;
      }
      .img-logo {
        display: none;
      }
      .img-group {
        display: flex;
        z-index: 20;
        height: 34vh;
        width: auto;
        position: relative;
        right: 0;
        left: 0;
        top: -6vh;
        margin-top: 11vh;
      }    
      .container {
        display: flex;
        width: 100vw;
        height: 100vh;
        flex-direction: column;
        align-items: center;
        .homeInfo{
          padding-bottom: 0vh;
          margin-top: 3vh;
        }
        .title{
          font-size: 3rem;
          margin-left: 0;
          margin-right: 0;
        }
      }
      .img-back_home1 {
        display: flex;
        position: absolute;
        width: 100vw;
        height: 105%;
      }
      .img-background2 {
        display: flex;
        position: absolute;
        height: 70%;
        right: 0;
        bottom: -50%;
      }
      .img-logo2 {
        display: flex;
        position: relative;
        height: 16vh;
        margin-top: 12vh;
      }
    }
    .img-group-aux {
      display: none;
    }
  }
  @include breakpoint($sm-tablet){
    .hiringMasterhead{
      .container{
        .title{
          font-size: 2.2rem;
        }
        .text{
          font-size: 1.8rem;
          width: 500px;
        }
      }
    }
  }
  